import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

const routes: Routes = [
  {
    path: 'authorization',
    loadChildren: () => import('./authorization/pages/authorization.module').then((m) => m.AuthorizationModule),
  },
  {
    path: 'permission',
    loadChildren: () => import('./permission/pages/permission.module').then((m) => m.PermissionModule),
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class FeaturesOpenBankingRoutingModule {}
